import axios from 'axios';
import { isLoggedIn, getSessionUser } from '../auth';

/**
 * Fetches the all user
 * @returns {Promise} user - a promise of user to be resolved.
 */
export function getUser() {
	if (isLoggedIn()) {
		const loggedUser = getSessionUser();
		return axios.get(`/v1/users/${loggedUser.id}`)
			.then(response => response);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Create a new user
 * @param {Object} userData - Object of the user information.
 * @returns {Promise} users - a promise of users to be resolved.
 */
export function createUser(userData) {
	if (isLoggedIn()) {
		return axios.post('/v1/register', userData);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Fetches the all users
 * @param {Number} limit - the query limit.
 * @param {Number} offset - the query offset.
 * @returns {Promise} users - a promise of users to be resolved.
 */
export function getUsers(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/users?limit=${limit}&offset=${offset}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Fetches the all users
 * @param {Number} cityId - the query limit.
 * @param {Number} stateId - the query offset.
 * @param {Number} careerId - the query limit.
 * @returns {Promise} users - a promise of filtered users to be resolved.
 */
export function filterUsers(query) {
	if (isLoggedIn()) {
		return axios.get(`/v1/users/filter?${query}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Fetches the a user using the id
 * @returns {Promise} user - a promise of user to be resolved.
 */
export function getUserProfile(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/users/${id}`)
			.then(response => response);
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

/**
 * Edits a user
 * @param {Object} userData - Object of the user information.
 * @returns {Promise} users - a promise of users to be resolved.
 */
export function updateUser(userData) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/users/${userData.id}`, userData);
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

/**
 * Edits a user
 * @param {Object} userData - Object of the user information.
 * @returns {Promise} avatar - a promise of users to be resolved.
 */
export function uploadAvatar(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/users/${id}/avatar`, { file: data });
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

